@use "../foundation"as *;

.l-header {
  position: relative;
  width: 100%;
  box-shadow: 0 5px 10px -5px rgba(143,167,179,.6);
  background: #fff;
  margin-bottom: 40px;
  // background: rgba($color: #000, $alpha: 0.65);
  &__inner {
    position: relative;
// padding: 0 120px;
    // border: 2px solid #1c1e22;
    border-top: none;
        // background: rgba($color: #1b1b1b, $alpha: 0.55);
        position: relative;
        height: 100%;

        // justify-content: center;

        // height: 64px;
      //   position: relative;
      //   @include flex($align:center, $justify:center);
      // width: 96vw;
      width: 98vw;
      margin: 0 auto;
      padding: 6px 24px 14px;
      @include mq-target(sm) {
        // padding: 8px 60px;
        // height: 84px;
      }
      @include mq-target(sm) {
        max-width: 1020px;
        // width: 100%;
      }
  }

  &__container {
    padding-top: 4px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__txt {
    text-align: left;
    font-size: 12px;
    color: $color-key;
  }

  &__right {
    display: flex;
    align-items: flex-end;
  }

  &-menu {
    &__list {
      display: flex;
    }

    &__item {
      min-width: 80px;
      max-width: 80px;
      padding: 0 4px;
      // height: 100%;
      line-height: 1;
      &:not(:last-child) {
        border-right: $color-border-waku;
      }

      &:last-child {
        padding-right: 0;
      }

      a {
        // font-weight: bold;
        color: $color-key;
        svg {
          height: 28px;
          width: auto;
        }

        span {
          // font-size: 12px;
          display: block;
          padding-top: 4px;
        }
      }
    }
  }

  &__logo {
    // @include absCenter;
    // margin-top: 14px;
    margin-right: 24px;
    // width: min(vwpc(190px),280px);
    position: relative;
    z-index: 600;
    // top: 12px;
    // left: 12px;
    a {
      font-weight: bold;
      // @include absCenter;
      font-size: 28px;
      // font-weight: bold;
      // font-style: italic;
      color: $color-font;
      // display: flex;
      display: block;
      width: 300px;
      line-height: 1;
      span {
        position: relative;
        // top: 4px;
        // color: $color-bg;
        font-size: 12px;
      }
    }
  }

  &__sns {
    margin-left: 40px;
    a {
      color: $color-white;
    }
  }

  &__wrap {
    &--pc {
      position: relative;
      @include flexCenter;
      z-index: 5;
      display: none;
      @include mq-target(sm) {
        display: block;
      }
    }

    &--sp {
      display: block;
      @include mq-target(sm) {
        display: none;
      }
    }
  }


  &-nav {
    &__list {
      @include flex($align:center);
    }
    &__item {
      margin-left: vw(14px, 769);
      font-size: vw(7px, 769);
      font-weight: 700;
    }
  }
}




