@use "../foundation"as *;


.p-rank {


    &__inner {

        position: relative;
        // padding-top: 14px;
        // padding: 12px 16px;
        margin-bottom: 0;
        // padding: 14px;
        
    }

    &-tab {
        &__item {
            cursor: pointer;
            width: 33%;

            span {
                color: #9e9e9e;
            }
        }
    }

    &__item {
        counter-increment: rank;
        position: relative;
        overflow: hidden;
        margin: 10px 0 0;
        padding: 0 0 10px;
        &:first-child {
            padding-top: 10px;
        }



        &:hover {
            transition: opacity .2s ease;
            opacity: 0.75;
          }

        &:first-child {
            border-top: none;
        }

        &:nth-of-type(1) {
            & .p-rank__count::before {
                background-color: #f7d305;
                background-image: linear-gradient(to top left,#f7bf05,#fcea7f,#fcd700,#f7d305,#eaca12,#ac9303,#f7d305,#fcea7f,#f7e705,#eaca12,#f7bf05);
            }
        }

        &:nth-of-type(2) {
            & .p-rank__count::before {
                background-color: #a5a5a5;
                background-image: linear-gradient(to top left,#a5a5a5,#e5e5e5,#ae9c9c,#a5a5a5,#a5a5a5,#7f7f7f,#a5a5a5,#e5e5e5,#a5a5a5,#a5a5a5,#a5a5a5);
            }
        }
        &:nth-of-type(3) {
            & .p-rank__count::before {
                background-color: #c47222;
                background-image: linear-gradient(to top left,#c46522,#e8b27d,#d07217,#c47222,#b9722e,#834c17,#c47222,#e8b27d,#c48022,#b9722e,#c46522);
            }
        }

        & .p-rank__count {

            &::before,
            &::after {
                content: "";
                position: absolute;
            }

            &::before {
                content: counter(rank);
                display: block;
                font-family: arial,helvetica,sans-serif;
                pointer-events: none;
                position: absolute;
                text-align: center;
                z-index: 100000000;
                background: $color-key;
                // background-color: #3c3c3c;
                background-image: repeating-linear-gradient(-45deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,.1) 6px,transparent 0,transparent 12px);
                left: calc(0.2em + 2px);
                top: -4px;
                line-height: 2.5;
                // height: 100px;
                font-size: 14px;
                width: 2em;
                border-radius: 0 0 4px 4px;
                box-shadow: 1px 1px 4px rgba(0,0,0,.2);
            }
            

            // &::after {
            //     font-style: italic;
            //     content: counter(rank);
            //     color: #fff;
            //     font-size: 14px;
            //     line-height: initial;
            //     width: 1.4em;
            //     filter: drop-shadow(1px 1px 1px #1b1b1b);
            //     left: .05em;
            //     top: .1em;
            //     z-index: 2;
            // }
        }
    }

    &__count {
        position: relative;
        width: 100%;
        overflow: hidden;
        // border-radius: 4px;
        
    }

    &-cont {
        &__img {
            // border-radius: 4px;
            overflow: hidden;
            // border: 1px solid #2a2a2a;
            // border: 0.1125vw solid #040404;
            // padding: 36.25% 0 0;
            // padding-top: 60.25%;
            aspect-ratio: auto 260 / 158;

        }
    }

    &-foot {
        position: relative;
        flex: 1;
        &__link {
            display: block;

            padding: 10px 8px 6px;
            background: linear-gradient(180deg,rgba(0,0,0,0) 0,rgba(0,0,0,.758) 20%,#000 100%);
            position: absolute;
            bottom: 0;
            width: 100%;
            box-sizing: border-box;
            margin-left: 0;
            h3 {
                font-weight: 600;
                font-size: min(vwpc(12px),13px);
                line-height: 1.4;
                -webkit-line-clamp: 3;
                filter: drop-shadow(1px 1px 1px #1b1b1b);
                color: #fff;
            }
        }

        &__data {
            // position: absolute;
            // top: 0;
            // right: 0;
            // opacity: 0.9;
            margin: 6px 0 0;
            display: flex;
            justify-content: space-between;
            display: none;

            & .c-cont-tag__list {
                margin: 0;
            }
        }
    }

    &__btn {
        padding: 12px 0 0;
    }
}


.p-prof {

    &__inner {
        // margin-top: 20px;
        overflow: hidden;
        position: relative;
        // margin: 0 12px;
        // background: url(../../assets/images/charaBg_04.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        // padding: 0 0 10px 0;
        // padding: 0;
        padding: 10px;
        border: $color-border-waku;
        

        &::before {

        }

        &::after {
            content: "";
            background: url(../../assets/images/kiritan.png);
            background-size: 500px auto;
            background-position: 100px -60px; 
            background-repeat: no-repeat;
            z-index: 22;
        }

        &::before {
            content: "";
            @include absAll;
            background: url(../../assets/images/bg.png);
            background-size: cover;
        }
    }


    &__img {
        position: absolute;
        top: -16px;
        right: 0;
        width: 220px;
        z-index: 1;
    }

    &-body {
        // padding: 10;
        min-height: 210px;
        @include flex($align:center);

        &__txt {
            z-index: 2;
            // border-radius: 4px;
            background: rgba($color: #000000, $alpha: 0.85);
            padding: 8px 4px 8px 8px;
            position: absolute;
            bottom: 0px;
            line-height: 1.5;
            left: 0;
            width: 100%;
            text-align: left;
            font-size: 14px;
            // position: relative;
            // padding: 20px;
            color: $color-font;
            background-color: rgba($color: #fff, $alpha: 0.75);
            border: 2px solid #00d1ff;


            &::before {
                content: '';
                position: absolute;
                display: block;
                width: 0;
                height: 0;
                left: 20px;
                top: -15px;
                border-right: 15px solid transparent;
                border-bottom: 15px solid #00d1ff;
                border-left: 15px solid transparent;
            }

            &::after {
                content: '';
                position: absolute;
                display: block;
                width: 0;
                height: 0;
                left: 20px;
                top: -12px;
                border-right: 15px solid transparent;
                border-bottom: 15px solid #fff;
                border-left: 15px solid transparent;
            }
        }

        &__img {
            width: 450px;
        }
    }
}

.twitter {
    transform: scale(0.85);
    /* サイズの拡大縮小の基準位置を決めるcss */
    transform-origin: left top;
}